
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';

type gameid = {
    params : {id: string}
}

export default async function GameDetail({params} : gameid) {
    
    const res = await fetch(`https://api.rawg.io/api/games/${params.id}?key=${process.env.RAWG_API_KEY}`)
    if (!res.ok) {
        throw new Error ('failed')
    } 
    const game = await res.json();

    return (
        <div className="container mx-auto ">
            <div className="flex py-20">
            <div className="relative rounded-xl shadow-md overflow-hidden">
            <img
            src={game.background_image}
            alt={game.name}
            className="w-150"
            />
            <div className="absolute bg-opacity-80 opacity-0 hover:opacity-100 transition-opacity duration-300 flex flex-col justify-between p-4">
            {game.short_screenshots?.map((img)=>
            <Swiper>
                
            </Swiper>
            )}
            </div>
            </div >
            <div className="flex justify-center w-1/2">
            <h1 className="text-4xl">{game.name}</h1>
            </div>
            </div>
        </div>
    );
}